/* 主题变量沿用 :root（src/index.module.css） */
.AboutPage {
	background: var(--cream-bg, #f8f6f2);
	color: var(--deep-navy, #1a1a2e);
}

.hero {
	max-width: 1080px;
	margin: 0 auto;
	padding: 56px 20px 24px;
	text-align: center;
}

.title {
	margin: 0 0 10px;
	font-size: 42px;
	font-weight: 300;
}

.subtitle {
	/* max-width: 860px; */
	margin: 0 auto;
	line-height: 1.8;
	color: #555;
	text-align: left;
}

.pillars {
	max-width: 1180px;
	margin: 24px auto 40px;
	padding: 0 20px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 16px;
}

.pillarCard {
	background: #fff;
	border-radius: 12px;
	padding: 16px;
	box-shadow: 0 6px 20px rgba(0,0,0,.06);
}

.pillarBadge {
	width: 28px;
	height: 4px;
	background: var(--accent-gold, #c6a972);
	border-radius: 2px;
	margin-bottom: 10px;
}

.pillarTitle { margin: 0 0 6px; font-size: 16px; font-weight: 700; }
.pillarDesc { margin: 0; color: #666; line-height: 1.7; }

.split {
	max-width: 1180px;
	margin: 0 auto 30px;
	padding: 0 20px;
	display: grid;
	grid-template-columns: 1.1fr 1fr;
	gap: 28px;
	align-items: center;
}

.reverse { grid-template-columns: 1fr 1.1fr; }
.reverse .splitMedia { order: 2; }
.reverse .splitContent { order: 1; }

.splitMedia img {
	width: 100%;
	height: 100%;
	max-height: 460px;
	object-fit: cover;
	border-radius: 14px;
	box-shadow: 0 10px 30px rgba(0,0,0,.10);
}

.splitContent h2 { margin: 0 0 10px; font-size: 26px; }
.splitContent p { margin: 0 0 10px; color: #555; line-height: 1.9; }

.stats {
	max-width: 900px;
	margin: 16px auto 40px;
	padding: 0 20px;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 12px;
	text-align: center;
}

.statItem { background: #fff; border-radius: 12px; padding: 16px; box-shadow: 0 6px 20px rgba(0,0,0,.06); }
.statItem strong { display: block; font-size: 24px; margin-bottom: 4px; color: var(--deep-navy, #1a1a2e); }
.statItem span { color: #666; }

.cta {
	max-width: 1080px;
	margin: 0 auto 60px;
	padding: 0 20px;
	text-align: center;
}

.ctaBtn {
	margin-top: 10px;
	padding: 10px 22px;
	border-radius: 999px;
	border: 1px solid var(--accent-gold, #c6a972);
	background: transparent;
	color: var(--accent-gold, #c6a972);
	cursor: pointer;
	transition: var(--transition, all .3s ease);
}

.ctaBtn:hover { background: var(--accent-gold, #c6a972); color: var(--light-text, #fff); }

/* 响应式 */
@media (max-width: 1024px) {
	.pillars { grid-template-columns: repeat(2, 1fr); }
	.split { grid-template-columns: 1fr; }
	.reverse .splitMedia { order: 1; }
	.reverse .splitContent { order: 2; }
}

@media (max-width: 640px) {
	.title { font-size: 30px; }
	.subtitle { font-size: 14px; }
	.stats { grid-template-columns: 1fr; }
}
